.restaurant-about {
  @extend %background-linear-gradient;
  padding: 16px 0 72px;
  @include respond-to('min', 'tablet') {
    // padding: 120px 0 96px;
    padding: 120px 0 87px;
    position: relative;
    height: 1280px;
  }
  @include respond-to('min', 'desktop') {
    padding: 120px 0 120px;
    height: 1275px;
  }
}
.restaurant-about__container {
  position: relative;
  @include respond-to('min', 'tablet') {
    display: flex;
    gap: 26px;
  }
  @include respond-to('min', 'desktop') {
    gap: 37px;
  }
}
.restaurant-about__wrapper-first {
  @include respond-to('min', 'tablet') {
    width: 439px;
  }
  @include respond-to('min', 'desktop') {
    width: 518px;
  }
}
.restaurant-about__slogan {
  padding: 0;
  margin: 0 0 12px;
  font-family: 'Times New Roman', Times, serif;
  color: #b4b4b4;
  // font-weight: 400;
  font-size: 20px;
  line-height: calc(23 / 20);
  @include respond-to('min', 'tablet') {
    font-size: 32px;
    line-height: calc(37 / 32);
    margin-bottom: 4px;
  }
}
.restaurant-about__logo {
  padding: 0;
  margin: 0 0 12px;
  font-family: 'Film P3';
  color: #494848;
  font-weight: 700;
  font-size: 40px;
  line-height: calc(43 / 40);
  letter-spacing: 0.3em;
  @include respond-to('min', 'tablet') {
    font-size: 64px;
    line-height: calc(69 / 64);
    margin-bottom: 24px;
  }
}
.restaurant-about__info-first {
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 12px;
  line-height: calc(14.6 / 12);
  letter-spacing: 0.02em;
  color: #5f5f5f;
  padding: 0;
  margin: 0 0 28px;

  @include respond-to('max', 'tablet') {
    width: 258px;
  }
  @include respond-to('min', 'tablet') {
    margin-bottom: 142px;
    font-size: 16px;
    line-height: calc(20 / 16);
  }
  @include respond-to('min', 'desktop') {
    margin-bottom: 162px;
  }
}
.restaurant-about__wrapper-inside {
  @include respond-to('max', 'tablet') {
    display: flex;
    // justify-content: space-between;
    gap: 20px;
    align-items: center;
    margin-bottom: 32px;
  }
}
.restaurant-about__wrapper-second {
  @include respond-to('min', 'tablet') {
    position: absolute;
    top: -161px;
    left: 465px;
  }
  @include respond-to('min', 'desktop') {
    position: absolute;
    top: -152px;
    left: 555px;
  }
}
.restaurant-about__info-second {
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 12px;
  line-height: calc(15 / 12);
  letter-spacing: 0.02em;
  color: #c79002;
  width: 163px;
  padding: 0;
  margin: 0;
  @include respond-to('min', 'tablet') {
    position: relative;
    left: -67px;
    margin: 0 0 156px;
    font-size: 16px;
    line-height: calc(19.5 / 16);
    width: 308px;
  }
  @include respond-to('min', 'desktop') {
    left: -61px;
    margin-bottom: 127px;
  }
}
.restaurant-about_photo-first {
  display: block;
  width: 244px;
  height: 322px;
  height: auto;
  @include respond-to('max', 'tablet') {
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
    border-radius: 2px;
    margin-bottom: 32px;
    margin-left: auto;
  }
  @include respond-to('min', 'tablet') {
    width: 350px;
    height: 500px;
    height: auto;
  }
  @include respond-to('min', 'desktop') {
    width: 460px;
    height: 574px;
    height: auto;
    box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.25));
  }
}
.restaurant-about_photo-second {
  display: block;
  // margin-bottom: 121px;
  // position: relative;

  width: 200px;
  height: 278px;
  height: auto;
  @include respond-to('max', 'phone') {
    box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
    border-radius: 2px;
  }
  @include respond-to('min', 'tablet') {
    width: 400px;
    height: 524px;
    height: auto;
    margin-bottom: 189px;
  }
  @include respond-to('min', 'desktop') {
    width: 460px;
    height: 574px;
    margin-bottom: 121px;
  }
}

