.restaurant-about-slider {
  @include respond-to('max', 'phone') {
    @include slider-two-images-before-phone(
      245px,
      25px,
      210px,
      // 215px,
      // 36px,
      // 34px,
      0px,
      150px
    );
  }
  @include respond-to('min', 'phone') {
    @include slider-two-images(245px, 25px, 205px);
    &__wrapper {
      left: 60px;
      // left: 185px;
      margin: 0;
    }
  }
  @include respond-to('min', 'tablet') {
    @include slider-two-images(194px, 40px, 335px);
    &__wrapper {
      left: 0;
      margin-left: 7px;
    }
  }
  @include respond-to('min', 'desktop') {
    @include slider-two-images(245px, 40px, 432px);
    &__wrapper {
      margin-left: 0px;
    }
  }
}
